import React, { FC } from 'react';
import { MenuList } from "./SideNav.styles";
import MenuItem from "./MenuItem/MenuItem";

interface SideNav {
    menuItems: string[]
}

const SideNav: FC<SideNav> = (sideMenuProps: SideNav) => {
    const menuItems: string[] = sideMenuProps.menuItems;
    const framerMotionVariants = {
        open: {
            transition: { staggerChildren: 0.07, delayChildren: 0.2 }
        },
        closed: {
            transition: { staggerChildren: 0.05, staggerDirection: -1 }
        }
    };

    return (
        <MenuList variants={framerMotionVariants}>
            {
                menuItems.map((menuItemData, index) => (
                    <MenuItem key={`${menuItemData}-${index}`} menuItemData={menuItemData} isSelected={index == 1} />
                ))
            }
        </MenuList>
    );
}

export default SideNav;
