import React, {FC, useEffect, useRef} from 'react';
import {NavMenu, NavMenuBody, NavMenuBlur} from "./SideMenu.styles";
import {useDimensions} from "../../utils/hooks/useDimensions";
import {useCycle} from "framer-motion";
import MenuToggle from "./SideNav/MenuToggle/MenuToggle";
import SideNav from "./SideNav/SideNav";
import {PageName} from "../../global/enums/global";

interface SideMenuProps {
}

const sidebar = (isBlur: boolean) => {
    return {
        open: (height = 1000) => ({
            clipPath: `circle(${height * (isBlur ? 3 : 2) + 200}px at 40px 40px)`,
            transition: {
                type: "spring",
                stiffness: 20,
                restDelta: 2
            }
        }),
        closed: {
            clipPath: "circle(30px at 40px 40px)",
            transition: {
                delay: 0.5,
                type: "spring",
                stiffness: 400,
                damping: 40
            }
        }
    }
};

const SideMenu: FC<SideMenuProps> = () => {
    const [isOpen, toggleOpen] = useCycle(false, true);
    const containerRef = useRef(null);
    const {height} = useDimensions(containerRef);

    const toggleEventHandler = () => {
        toggleOpen();
        document.body.style.overflowY = isOpen ? '' : 'hidden';
    }

    return (
        <NavMenu
            initial={false}
            animate={isOpen ? "open" : "closed"}
            custom={height}
            whileInView={{height: isOpen ? 'auto' : '0'}}
            ref={containerRef}>
            <NavMenuBody variants={sidebar(false)}/>
            <SideNav menuItems={Object.values(PageName)}/>
            <NavMenuBlur variants={sidebar(true)} onClick={() => toggleEventHandler()}/>
            <MenuToggle toggleEventHandler={() => toggleEventHandler()}/>
        </NavMenu>
    );
}

export default SideMenu;
