import React, {FC} from "react";
import {MenuListItem, SelectedListItem, SelectedItemText, InactiveItemText, MiniDivider} from "./MenuItem.styles"

interface MenuItemProps {
    menuItemData: string,
    isSelected?: boolean
}


const variants = {
    open: {
        y: 0,
        opacity: 1,
        transition: {
            y: {stiffness: 1000, velocity: -100}
        }
    },
    closed: {
        y: 50,
        opacity: 0,
        transition: {
            y: {stiffness: 1000}
        }
    }
};

const MenuItem: FC<MenuItemProps> = (menuItemProps: MenuItemProps) => {
    const menuItemData: string = menuItemProps.menuItemData;
    const isSelected: boolean = menuItemProps.isSelected ?? false;

    return (<React.Fragment>
        {isSelected
            ? <SelectedListItem variants={variants} whileHover={{scale: 1.1}} whileTap={{scale: 0.95}}>
                <MiniDivider/>
                <SelectedItemText> {menuItemData} </SelectedItemText>
            </SelectedListItem>
            : <MenuListItem variants={variants} whileHover={{scale: 1.1}} whileTap={{scale: 0.95}}>
                <InactiveItemText>{menuItemData}</InactiveItemText>
            </MenuListItem>}
    </React.Fragment>);
}

export default MenuItem;
